.header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	&__logo {
		width: 250px;
		img {
			width: 100%;
		}
	}
	&__offical {
		color: rgb(255, 255, 255);
		font-size: 12px;
		font-weight: 600;
		text-align: center;
		span {
			display: block;
			font-size: 18px;
		}
	} 
	&__phone {
		display: block;
		color: rgb(255, 255, 255);
		font-size: 18px;
		font-weight: 700;
		text-decoration: none;
		&:hover {
			color: #ffff;

		}
	}
	&__contacts {
		display: flex;
		flex-direction: column;
		align-items:flex-end;
	}
}